import axios from "axios";
import React from "react";
import { Link } from 'react-router-dom';
import { useState } from "react";

//const baseURL = "https://api.openweathermap.org/data/2.5/weather?q=mumbai&units=metric&APPID=7f430f00a931c8c287f021c622cb5f8e";

const baseURL = "https://newsapi.org/v2/everything?q=bitcoin&from=2021-10-25&sortBy=publishedAt&apiKey=6cc4c09c9d374e3eaba3bf30ee379d73";

export default function App() {
  const [post, setPost] = React.useState(null);
  const [accepted, setAccepted] = useState(false);

  React.useEffect(() => {

    setTimeout(() => {

      axios.get(baseURL).then((response) => {
        setPost(response.data.articles);
        console.log(JSON.stringify(response.data.articles));
      });
      setAccepted(!accepted);

    }, 100000);
    
  },[accepted]);

  if (!post) return null;

  return (
    <div>
      <Link to="/">
				<h2 className="heading">Home</h2>
			</Link>
      <br/>
      <div className="weatherApi">

      <h4>NEWS</h4>
      <hr/>
      {/* <p><strong>Location: Mumbai</strong></p> */}
      {post.filter((ele,index) => index < 5).map((item) => (
        <>
        <div className="newsApi">
        <p>{item.title}</p>
        <p><strong>Published At: </strong>{item.publishedAt}</p>
        </div>
        <hr/>
        </>
      ))}
      {/* <p>Temp_Min:{post.main.temp_Min}</p>
      <p>Temp_Max: {post.main.temp_max}</p> */}
      </div>
    </div>
  );
}